export async function getServerSideProps({ query }) {
  const res = await fetch(`http://3.21.165.5:1337/artists?name=${query.name}&lastname=${query.lastname}`)
  const artist = await res.json()

  return {
    props: {
      artist: artist[0]
    }
  }
}

export default Artist